.post-img-container > img {
    max-width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
    position: relative;
}

.loading {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0;
}

.loading > div {
    height: 100px;
    width: 100px;
    border: 8px solid black;
    border-color: black black black transparent;
    border-radius: 50%;
    animation: loading 1s linear infinite;
}

@keyframes loading {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.name {
    font-size: 2em;
    margin: 0;
}

.date {
    font-size: 1.25em;
    margin: 0;
}

.hidden {
    display: none;
}

.series-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.series-list img {
    max-width: 150px;
    margin: 0px 10px;
}

.celebrity-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.celebrity-list a {
    color: initial;
    text-decoration: none;
    border: 2px solid black;
    border-radius: 5px;
    padding: 2.5px 5px;
    background-color: white;
    margin: 5px 5px;
    font-size: .75em;
}